.App {
  text-align: center;
  font-family: 'Arial', sans-serif;
  margin-top: 50px;
  background-color:wheat;
  color: #333;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  position: relative;
}

/* Highlighting the app name */
.App-name {
  font-size: 2em;
  margin-bottom: 20px;
  color: #ff6b6b; /* Highlight color */
}

.header-left,
.header-right {
  position: absolute;
  top: 20px;
}

.header-left {
  left: 20px;
}

.header-right {
  right: 20px;
}

.login-container {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff; /* White background */
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.error {
  color: red;
  margin-bottom: 15px;
}

form div {
  margin-bottom: 15px;
}

form label {
  margin-right: 10px;
  color: #555; /* Label color */
}

form input {
  padding: 12px;
  border-radius: 6px;
  border: 1px solid #ccc;
  transition: border-color 0.3s ease;
}

form input:focus {
  outline: none;
  border-color: #007bff; /* Focus color */
}

form button {
  padding: 12px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

form button:hover, .logout-btn:hover {
  background-color: #0056b3;
}

.App {
  text-align: center;
  font-family: 'Arial', sans-serif;
  margin-top: 50px;
}

.blue-container {
  background-color: #007bff; /* Blue background */
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.container-fluid {
  max-width: 800px;
  margin: 0 auto;
}

.App-name {
  font-size: 2.5em;
  margin-bottom: 20px;
  color: white; /* Text color */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  background-image: linear-gradient(45deg, #ff6b6b, #ffc65b);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

