.demo-container {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  max-width: 400px;
  margin: 50px auto;
  padding: 30px;
  border-radius: 12px;
  background-color: #f9f9f9;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  background-image: linear-gradient(45deg, #ff9a9e, #fad0c4);
}

.welcome-container,
.login-container,
.register-container {
  text-align: center;
  margin-top: 20px;
}

input {
  width: 100%;
  padding: 15px;
  margin-top: 15px;
  border: none;
  border-radius: 6px;
  font-size: 18px;
  background-color: #f5f5f5;
  transition: background-color 0.3s ease;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
}

input:focus {
  outline: none;
  background-color: #e0e0e0;
}

button {
  width: 100%;
  padding: 15px;
  margin-top: 25px;
  border: none;
  border-radius: 6px;
  background: linear-gradient(45deg, #ff6b6b, #ffc65b);
  color: white;
  font-size: 18px;
  cursor: pointer;
  transition: transform 0.2s, background 0.3s;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

button:hover {
  background: linear-gradient(45deg, #ff4b4b, #ffaa00);
  transform: scale(1.05);
}

h3 {
  color: #333;
  margin-bottom: 20px;
}

select {
  width: 100%;
  padding: 15px;
  margin-top: 15px;
  border: none;
  border-radius: 6px;
  font-size: 18px;
  background-color: #f5f5f5;
  transition: background-color 0.3s ease;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
}

select:focus {
  outline: none;
  background-color: #e0e0e0;
}
